<template>
  <div id="#app">
    <h1 class="title">Type The Alphabet</h1>
    <h2 class="subtitle">ËæìÂÖ•Â≠óÊØçÔºåÁúãÁúã‰Ω†ÁöÑÈÄüÂ∫¶ÊúâÂ§öÂø´ :)</h2>
    <div class="status">
      <p class="msg">{{ upperCaseCurr }}</p>
    </div>
    <div class="game">
      <input
        type="text"
        class="game-input"
        v-model="inputVal"
        placeholder="Âú®ËøôÈáåËæìÂÖ•"
        @keydown="handleKeyup"
        ref="refInput"
      />
      <input
        type="submit"
        class="game-submit"
        value="ÈáçÁΩÆ"
        @click="handleReset"
      />
    </div>
    <div class="timer">Êó∂Èó¥Ôºö{{ showTime ? showTime : "0.000" }}s</div>
    <div class="result" v-show="records.length">
      <p>ÊàêÁª© üèÅ</p>
      <ul>
        <li v-for="(v, i) in records" :key="`records-${i}`">
          <span>{{ v.display }}: </span> <span>{{ v.currTime }}s</span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      inputVal: "",
      curr: "a", // ÂΩìÂâçÁöÑÂ≠óÊØç
      alpha: this.createAlpha(), // Èó≠ÂåÖÂàõÂª∫ÂΩìÂâçÂ≠óÊØç
      timer: "", //È°µÈù¢Êó∂Èó¥ÊòæÁ§∫ÁöÑÂÆöÊó∂Âô®
      showTime: 0, // È°µÈù¢ÊòæÁ§∫Êó∂Èó¥
      startTime: "", //ÂºÄÂßãÊó∂Èó¥
      records: [],
    };
  },
  computed: {
    upperCaseCurr() {
      return this.curr.toUpperCase();
    },
  },
  methods: {
    initTimer() {
      this.timer = setInterval(() => {
        this.showTime = ((Date.now() - this.startTime) * 0.001).toFixed(3);
      }, 1);
    },
    handleKeyup(e) {
      // Âè™ÊúâËæìÂÖ•Â≠óÊØçÊâçÊúâÁõ∏Â∫îÔºåÂÖ∂‰ªñÊåâÈîÆÊó†Êïà
      if (e.keyCode > 64 && e.keyCode < 91) {
        // Âè™ÊúâËæìÂÖ•ÂΩìÂâçÊòæÁ§∫ÁöÑÂ≠óÊØçÊâçÁõ∏Â∫îÔºåÂÖ∂‰ªñÊó†Êïà
        if (e.key === this.curr) {
          const currTime = Date.now();
          // Â¶ÇÊûúÂΩìÂâçÊòØaÈÇ£‰πàÂ∞±ËÆæÁΩÆÂΩìÂâçÊó∂Èó¥‰∏∫ÂºÄÂßãÊó∂Èó¥, Âπ∂ÂêØÂêØÂä®ÂÆöÊó∂Âô®Êõ¥Êñ∞
          if (e.key === "a") {
            this.startTime = currTime;
            this.initTimer();
          }
          this.records.push({
            display: this.curr,
            currTime: ((currTime - this.startTime) / 1000).toFixed(3),
          });
          // Â¶ÇÊûúËæìÂÖ•Âà∞zËØ¥ÊòéËæìÂÖ•Âà∞ÊúÄÂêé‰∏Ä‰∏™Â≠óÊØçÔºåÁªìÊùüÂÆöÊó∂Âô®
          if (e.key === "z") {
            clearInterval(this.timer);
            // Áî±‰∫éÂÆöÊó∂Âô®Â≠òÂú®Êó∂Èó¥Â∑ÆÔºåÊúÄÂêé‰∏Ä‰∏™Êï∞ÂíåÊòæÁ§∫ÁöÑ‰∏çÁªü‰∏ÄÔºå
            // Âõ†Ê≠§Â∞ÜÊúÄÂêéÁöÑËÆ∞ÂΩïÁöÑÁªìÊûúËµãÂÄºÂà∞È°µÈù¢‰∏ä„ÄÇ
            this.showTime = this.records.slice(-1)[0].currTime;
          }
          //Áî®Èó≠ÂåÖÁîüÊàê‰∏ã‰∏Ä‰∏™Â≠óÊØçÔºåÂπ∂ÊääÂÆÉËµãÂÄºÂà∞ÂΩìÂâç
          this.curr = this.alpha();
        } else {
          e.preventDefault();
        }
      } else {
        e.preventDefault();
      }
    },
    handleReset() {
      this.inputVal = ""; // ÁΩÆÁ©∫ input
      this.curr = "a"; // ÈáçËÆæÁΩÆÂΩìÂâçÂ≠óÊØç‰∏∫ÂàùÂßãÂÄºa
      this.alpha = null; // ÁΩÆÁ©∫Âπ∂ÈáçÊñ∞ÂàõÂª∫Èó≠ÂåÖ
      this.alpha = this.createAlpha();
      this.startTime = ""; // ÁΩÆÁ©∫ÂºÄÂßãÊó∂Èó¥ÂíåËÆ∞ÂΩï
      this.records = [];
      clearInterval(this.timer);
      this.showTime = 0;
      this.$refs.refInput.focus();
    },
    createAlpha() {
      let alpha = "a";
      return function () {
        switch (alpha) {
          case "a":
            alpha = "b";
            break;
          case "b":
            alpha = "c";
            break;
          case "c":
            alpha = "d";
            break;
          case "d":
            alpha = "e";
            break;
          case "e":
            alpha = "f";
            break;
          case "f":
            alpha = "g";
            break;
          case "g":
            alpha = "h";
            break;
          case "h":
            alpha = "i";
            break;
          case "i":
            alpha = "j";
            break;
          case "j":
            alpha = "k";
            break;
          case "k":
            alpha = "l";
            break;
          case "l":
            alpha = "m";
            break;
          case "m":
            alpha = "n";
            break;
          case "n":
            alpha = "o";
            break;
          case "o":
            alpha = "p";
            break;
          case "p":
            alpha = "q";
            break;
          case "q":
            alpha = "r";
            break;
          case "r":
            alpha = "s";
            break;
          case "s":
            alpha = "t";
            break;
          case "t":
            alpha = "u";
            break;
          case "u":
            alpha = "v";
            break;
          case "v":
            alpha = "w";
            break;
          case "w":
            alpha = "x";
            break;
          case "x":
            alpha = "y";
            break;
          case "y":
            alpha = "z";
            break;
          case "z":
            alpha = "ÂÆåÊàê";
            break;
          default:
            break;
        }
        return alpha;
      };
    },
  },
};
</script>

